<template>
    <div>{{ text }}
        <datepicker v-model="date" format="yyyy-MM-dd" :language="language" :inline="false" @selected="onDateSelected"></datepicker>
    </div>
  </template>
  
  <script>
  import Datepicker from 'vuejs-datepicker';
  import { ja } from 'vuejs-datepicker/dist/locale';
  export default{
    components: {
      Datepicker,
    },
    data() {
      return {
        year:0,
        month:0,
        date:0,
        date: new Date(),
        text: "aaa",
        language: ja,
      };
    },
    methods: {
        onDateSelected(date){
            console.log(date);
            console.log(date.getFullYear());
            console.log(date.getMonth());
            console.log(date.getDate());

            this.year = date.getFullYear();
            this.month = date.getMonth() + 1;
            this.date = date.getDate();

            this.text=this.year + "-" + this.month + "-" + this.date;
            console.log(this.text);
        },
    }
  };
</script>

<style lang="scss">
*{
  box-sizing: border-box;
}
</style>